import { Injectable } from '@angular/core';
import {Statistiques} from '../combat-session/personnage/statistiques';

@Injectable({
  providedIn: 'root'
})
export class StatistiquesService {

  statistiques: Statistiques;

  constructor() { }

  setStatistique(statistiques: Statistiques) {
    this.statistiques = statistiques;
  }

  getStatistique(): Statistiques {
    return this.statistiques;
  }

  clearService() {
    this.statistiques = undefined;
  }
}
