import {AfterViewInit, Component, OnDestroy, OnInit} from '@angular/core';
import {PersonnageAddCompetenceEffectComponent} from '../personnage-add-competence-effect/personnage-add-competence-effect.component';
import {CompetenceAddCompetenceEffectComponent} from '../competence-add-competence-effect/competence-add-competence-effect.component';
import {EffectAddCompetenceEffectComponent} from '../effect-add-competence-effect/effect-add-competence-effect.component';
import {BASE_URL} from '../../service/rest.service';
import {map} from 'rxjs/operators';
import {Observable, Subscription} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {StatistiquesService} from '../../service/statistiques.service';
import {ActivatedRoute} from '@angular/router';
import {EffectAddCompetenceEffectService} from '../../service/effect-add-competence-effect.service';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {NgbdModalConfirm, NgbdModalConfirmAutofocus} from '../../modal-focus/modal-focus';

const MODALS = {
  focusFirst: NgbdModalConfirm,
  autofocus: NgbdModalConfirmAutofocus,
};

@Component({
  selector: 'app-add-competence-effect',
  templateUrl: './add-competence-effect.component.html',
  styleUrls: ['./add-competence-effect.component.scss']
})
export class AddCompetenceEffectComponent implements OnInit, AfterViewInit, OnDestroy {

  public idPersonnage = -1;
  public idCompetence = -1;
  public idEffect = -1;

  private creatingSkill = false;

  public personnagesList: PersonnageAddCompetenceEffectComponent[] = [];
  public competenceList: CompetenceAddCompetenceEffectComponent[] = [];
  public effectList: EffectAddCompetenceEffectComponent[] = [];

  private personnage: PersonnageAddCompetenceEffectComponent;
  public competence: CompetenceAddCompetenceEffectComponent;

  contextmenu = false;
  contextmenuX = 0;
  contextmenuY = 0;
  private righClickCompetence: number;
  private rightClickedCompetenceName: string;

  public scrollbarOptions = { axis: 'y', theme: 'rounded-dots' };

  constructor(private statistiquesService: StatistiquesService,
              private effectAddCompetenceEffectService: EffectAddCompetenceEffectService,
              private http: HttpClient,
              private route: ActivatedRoute,
              private modalService: NgbModal,
              ) {

    this.route.params.subscribe(params => {
      this.idPersonnage = params.idPersonnage === undefined ? -1 : this.idPersonnage;
      this.idCompetence = params.idCompetence === undefined ? -1 : this.idCompetence;
      this.idEffect = params.idEffect === undefined ? -1 : this.idEffect;
    });
    effectAddCompetenceEffectService.loadService(this.http);
    this.effectAddCompetenceEffectService.addEffectCompetenceComponent = this;
  }

  ngOnInit() {
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/personnagesAddCompetenceEffectRest.php';
    const personnageAddCompetenceEffectComponentObservable: Observable<PersonnageAddCompetenceEffectComponent[]> =
      this.http.get<PersonnageAddCompetenceEffectComponent[]>(baseUrlBis).pipe(
        // Adapt each item in the raw data array
        map((data: PersonnageAddCompetenceEffectComponent[]) => data.map(item => item)),
      );
    const personnageAddCompetenceEffectComponentSuscription: Subscription = personnageAddCompetenceEffectComponentObservable.subscribe(
      (value) => {
        for (const val of value) {
          const newPersonnage = new PersonnageAddCompetenceEffectComponent(this.statistiquesService,
            this.effectAddCompetenceEffectService, this.http, this.route, this.modalService);
          newPersonnage.adaptFromInside(val);
          this.personnagesList.push(newPersonnage);
        }
        // this.personnagesList = value;
        personnageAddCompetenceEffectComponentSuscription.unsubscribe();
      },
      (error) => {
        console.log('Une erreur est survenue dans la suscription: ' + error);
      },
      () => {
      }
    );
  }

  ngAfterViewInit() {
    // @ts-ignore
  }

  ngOnDestroy() {
  }

  notifyPersonnageSelection(idPersonnage: number) {
    this.competenceList = [];
    this.idPersonnage = idPersonnage;
    this.personnage = this.personnagesList.find(personnage => personnage.idPersonnage === this.idPersonnage);
    this.competenceList = this.personnage.getCompetences();
    document.getElementById('imageSelectedPersonnage').setAttribute('src', this.personnage.icon);
  }

  reloadCompetences() {
    if (this.idPersonnage !== -1 && this.idPersonnage !== undefined && this.personnage !== null && this.personnage !== undefined) {
      this.competenceList = this.personnage.getCompetences();
    }
  }

  notifyCompetenceSelection(idCompetence: number) {
    this.effectList = [];
    this.idCompetence = idCompetence;
    this.competence = this.competenceList.find(competence => competence.idCompetence === this.idCompetence);
    this.effectList = this.competence.effectList;
  }

  public getCompetenceList(): CompetenceAddCompetenceEffectComponent[] {
    return this.competenceList;
  }

  public addCompetenceToList() {

    if (!this.creatingSkill) {
      const input = document.createElement('input');
      input.classList.add('form-control');
      input.style.width = '100%';
      input.id = 'inputAddCompetenceToList';

      const div = document.createElement('div');
      div.classList.add('col-md-3');
      div.id = 'divAddCompetenceToList';
      div.style.maxWidth = '100%';
      div.style.margin = '5px';
      div.style.marginRight = '15px';
      div.style.paddingLeft = '0px';
      div.style.paddingRight = '7px';
      div.appendChild(input);

      const thisAddEffect = this;

      input.addEventListener('keypress', e => {
        if (e.key === 'Enter') {
          thisAddEffect.open('autofocus');
        }
      });
      input.addEventListener('keydown', e => {
        if (e.key === 'Escape') {
          console.log('Escape');
          document.getElementById('inputAddCompetenceToList').remove();
          document.getElementById('divAddCompetenceToList').remove();
          this.creatingSkill = !this.creatingSkill;
        }
      });

      const list = document.getElementById('competenceList');
      list.prepend(div);
      this.creatingSkill = !this.creatingSkill;
      input.focus();
    }
  }

  open(name: string) {
    const input = document.getElementById('inputAddCompetenceToList') as HTMLInputElement;
    const modalRef = this.modalService.open(MODALS[name]);
    modalRef.componentInstance.modalTitle = 'CompetenceEffect deletion';
    modalRef.componentInstance.modalContent = '<p>' +
      '<strong>Es-tu sr de vouloir ajouter <span class="text-primary">"' + input.value + '"</span> comme comptence, ' +
      '  <span class="text-info">' + this.personnage.libelle + '</span> ?</strong>' +
      '</p>    ' +
      '<p>Toute information associe  cet comptence sera ajoute de manire permanente.   ' +
      ' <span class="text-warning">Cette opration ne pourra tre annule.</span>    ' +
      '</p>';

    modalRef.result.then(
      () => {
        this.effectAddCompetenceEffectService.addCompetenceToCharacter(input.value).then((data: any) => {
          console.log(data);
          document.getElementById('inputAddCompetenceToList').remove();
          document.getElementById('divAddCompetenceToList').remove();
          this.creatingSkill = !this.creatingSkill;
          this.competenceList = this.personnage.getCompetences();
        });
      });
  }

  onrightClick(event, idCompetence: number, competenceName: string) {
    event.preventDefault();
    event.stopPropagation();
    this.contextmenuX = event.clientX;
    this.contextmenuY = event.clientY;
    this.contextmenu = true;
    this.righClickCompetence = idCompetence;
    this.rightClickedCompetenceName = competenceName;
  }

  // disables the menu
  disableContextMenu() {
    this.contextmenu = false;
  }



}
