import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class Targets {

  public idCompetenceUse: number;
  public idCompetence: number;
  public idLauncher: number;
  public idReceiver: number;
  public turnUse: number;

  constructor() { }


  adaptFromOutside(item: any) {
    this.idCompetenceUse = item.idCompetenceUse;
    this.idCompetence = item.idCompetence;
    this.idLauncher = item.idLauncher;
    this.idReceiver = item.idReceiver;
    this.turnUse = item.turnUse;
  }

}
