import {Component, Input, OnInit} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {map} from 'rxjs/operators';
import {Subscription} from 'rxjs';
import {BASE_URL} from '../../service/rest.service';

@Component({
  selector: 'app-competence-icon',
  templateUrl: './competence-icon.component.html',
  styleUrls: ['./competence-icon.component.scss']
})
export class CompetenceIconComponent implements OnInit {

  // tslint:disable-next-line:variable-name
  @Input() public Id_Competence: number;
  public Libelle: string;
  public Image: string;
  public Niveau: number;

  constructor(private http: HttpClient) { }

  ngOnInit() {
    this.loadItSelf();
  }

  public loadItSelf() {
    // console.log(this.Id_Competence);
      const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/competenceIconAngularRest.php?id='
      + this.Id_Competence;
      const competenceIconObservable = this.http.get<CompetenceIconComponent>(baseUrlBis).pipe(
      // Adapt each item in the raw data array
      map((item: CompetenceIconComponent) => this.adaptFromInside(item)),
    );
      const competenceIconSubsciption: Subscription = competenceIconObservable.subscribe(
      () => {
        competenceIconSubsciption.unsubscribe();
      },
      (error) => {
        console.log('Une erreur est survenue dans la suscription de la competence: ' + error);
      },
      () => {
      }
    );
  }

  adaptFromInside(item: any) {
    this.Id_Competence = item.Id_Competence;
    this.Libelle = item.Libelle;
    this.Image = item.Image;
    this.Niveau = item.Niveau;
  }

}
