import {Component, Input, OnInit} from '@angular/core';
import {NgbdModalConfirm, NgbdModalConfirmAutofocus} from '../../modal-focus/modal-focus';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';
import {EffectAddCompetenceEffectService} from '../../service/effect-add-competence-effect.service';

const MODALS = {
  focusFirst: NgbdModalConfirm,
  autofocus: NgbdModalConfirmAutofocus,
};

@Component({
  selector: 'app-context-menu-competence',
  templateUrl: './context-menu-competence.component.html',
  styleUrls: ['./context-menu-competence.component.scss']
})
export class ContextMenuCompetenceComponent implements OnInit {

  @Input() x = 0;
  @Input() y = 0;
  @Input() idCompetence = 0;
  @Input() idPersonnage = 0;
  @Input() competenceName = '';

  constructor(private modalService: NgbModal,
              private effectAddCompetenceEffectService: EffectAddCompetenceEffectService) {
  }

  ngOnInit() {
  }

  skillDeletionPopover(name: string) {
    this.effectAddCompetenceEffectService.addEffectCompetenceComponent.disableContextMenu();
    const modalRef = this.modalService.open(MODALS[name]);
    modalRef.componentInstance.modalTitle = 'CompetenceEffect deletion';
    modalRef.componentInstance.modalContent = '<p>' +
      '<strong>Es-tu sr de vouloir supprimer la comptence <span class="text-primary">"' + this.competenceName + '"</span> ?</strong>' +
      '</p>    ' +
      '<p>Toute information associe  cette comptence sera supprime de manire permanente.   ' +
      ' <span class="text-danger">Cette opration ne pourra tre annule.</span>    ' +
      '</p>';

    modalRef.result.then(
      (result) => {
        this.effectAddCompetenceEffectService.deleteCompetence(this.idCompetence, this.idPersonnage);
      }, (reason => { }));
  }

}
