import {Component, OnInit} from '@angular/core';
import {BASE_URL} from '../../service/rest.service';
import {Subscription} from 'rxjs';
import {map} from 'rxjs/operators';
import {HttpClient} from '@angular/common/http';
import {ActivatedRoute} from '@angular/router';
import {StatistiquesService} from '../../service/statistiques.service';
import {Statistiques} from '../../combat-session/personnage/statistiques';
import {faQuestionCircle} from '@fortawesome/free-solid-svg-icons';
import {EffectAddCompetenceEffectService} from '../../service/effect-add-competence-effect.service';

@Component({
  selector: 'app-effect-add-competence-effect',
  templateUrl: './effect-add-competence-effect.component.html',
  styleUrls: ['./effect-add-competence-effect.component.scss']
})
export class EffectAddCompetenceEffectComponent implements OnInit {

  faQuestionCircle = faQuestionCircle;

  public idCompetenceEffect: number;
  public idCompetence: number;
  public effectOrder: number;
  public actionType: number;
  public effectType: number;
  public applicationType: number;
  public niveau: number;
  public niveauRequis: number;
  public descriptionBefore: string;
  public descriptionAfter: string;
  public typeCalcul: number;
  public calcul1A: number;
  public calcul1B: number;
  public calcul2A: number;
  public calcul2B: number;
  public amplitude: number;
  public nombreAmplitude: number;
  public statistique1: string;
  public statistique2: string;
  public impact: string;
  public pourcentage: boolean;
  public numberOfUse: number;
  public numberOfTurn: number;
  public numberOfFight: number;
  public numberOfTarget: number;
  public accumulationType: number;
  public numberOfAccumulation: number;
  public successiveAccumulation: boolean;
  public linkedEffect: boolean;


  private validationText: string;
  public statistiques: Statistiques;


  constructor(private http: HttpClient,
              private route: ActivatedRoute,
              private statistiquesService: StatistiquesService,
              private effectAddCompetenceEffectService: EffectAddCompetenceEffectService) {
    this.setStatistiques(this.statistiquesService.getStatistique());
  }

  public init() {
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/effectAddCompetenceEffectRest.php?id=' + this.idCompetenceEffect;
    const effectAddCompetenceEffectComponentObservable = this.http.get<EffectAddCompetenceEffectComponent>(baseUrlBis).pipe(
      // Adapt each item in the raw data array
      map((item: EffectAddCompetenceEffectComponent) => this.adaptFromInside(item)),
    );
    const effectAddCompetenceEffectComponentSuscription: Subscription = effectAddCompetenceEffectComponentObservable.subscribe(
      () => {
        effectAddCompetenceEffectComponentSuscription.unsubscribe();
      },
      (error) => {
        console.log('Une erreur est survenue dans la suscription: ' + error);
      },
      () => {
      }
    );
  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.idCompetenceEffect = params.idEffect;
      if (this.idCompetenceEffect !== undefined || +this.idCompetenceEffect !== -1) {
        this.init();
        this.setValidateButtonText();
        this.effectAddCompetenceEffectService.effectAddEffectCompetenceComponent = this;
      }
    });
  }

  public adaptFromInside(item: any) {
    this.idCompetenceEffect = item.idCompetenceEffect;
    this.idCompetence = item.idCompetence;
    this.effectOrder = item.effectOrder;
    this.actionType = item.actionType;
    this.effectType = item.effectType;
    this.applicationType = item.applicationType;
    this.niveau = item.niveau;
    this.niveauRequis = item.niveauRequis;
    this.descriptionBefore = item.descriptionBefore;
    this.descriptionAfter = item.descriptionAfter;
    this.typeCalcul = item.typeCalcul;
    this.calcul1A = item.calcul1A;
    this.calcul1B = item.calcul1B;
    this.calcul2A = item.calcul2A;
    this.calcul2B = item.calcul2B;
    this.amplitude = item.amplitude;
    this.nombreAmplitude = item.nombreAmplitude;
    this.statistique1 = item.statistique1;
    this.statistique2 = item.statistique2;
    this.impact = item.impact;
    this.pourcentage = item.pourcentage;
    this.numberOfUse = item.numberOfUse;
    this.numberOfTurn = item.numberOfTurn;
    this.numberOfFight = item.numberOfFight;
    this.numberOfTarget = item.numberOfTarget;
    this.accumulationType = item.accumulationType;
    this.numberOfAccumulation = item.numberOfAccumulation;
    this.successiveAccumulation = item.successiveAccumulation;
    this.linkedEffect = item.linkedEffect;
  }

  public setStatistiques(statistiques: Statistiques) {
    this.statistiques = statistiques;
  }

  public getEffectValue() {
    if (+this.typeCalcul === 1) {
      return +this.calcul1A + (Math.floor(+this.getStatistique(this.statistique1) / +this.calcul1B));
    } else if (+this.typeCalcul === 2) {
      return ((+this.calcul1A + (Math.floor(+this.getStatistique(this.statistique1) / +this.calcul1B)))
        + (+this.calcul2A + (Math.floor(+this.getStatistique(this.statistique2) / +this.calcul2B))));
    }
  }

  public getStatistique(statistique: string): number {
    switch (statistique) {
      case 'intelligence':
        return +this.statistiques.Intelligence + +this.statistiques.BonusIntelligence;
      case 'force':
        return +this.statistiques.Force + +this.statistiques.BonusForce;
      case 'agilite':
        return +this.statistiques.Agilite + +this.statistiques.BonusAgilite;
      case 'ressource':
        return +this.statistiques.Ressource + +this.statistiques.BonusRessource;
      case 'vitalite':
        return +this.statistiques.Vitalite + +this.statistiques.BonusVitalite;
      case 'niveau':
        return +this.niveau;
    }
  }

  public getEffectDescription(): string {
    let description = '';
    const calcul = this.getEffectValue();
    switch (+this.actionType) {
      case 1:
      case 2:
        description += 'Inglige ';
        break;
      case 3:
      case 4:
        description += 'Vole ';
        break;
      case 5:
        description += 'Soigne ';
        break;
      case 6:
        description += 'Donne';
        break;
      case 7:
        if (calcul > 0) {
          description += 'Augmente ';
        } else if (calcul < 0) {
          description += 'Diminue ';
        }
        break;
      case 8:
        description += 'Avant de faire une action, ';
        break;
      case 9:
        description += 'Aprs avoir fait une action, ';
        break;
      case 10:
        description += 'Avant de lancer une attaque, ';
        break;
      case 11:
        description += 'Aprs avoir lanc une attaque, ';
        break;
      case 12:
        description += 'Avant de lancer une comptence, ';
        break;
      case 13:
        description += 'Aprs avoir lanc une comptence, ';
        break;
      case 14:
        description += 'Avant d\'infliger des dgts, ';
        break;
      case 15:
        description += 'Aprs avoir inflig des dgts, ';
        break;
      case 16:
        description += 'Avant d\'infliger des dgts physiques, ';
        break;
      case 17:
        description += 'Aprs avoir inflig des dgts physique, ';
        break;
      case 18:
        description += 'Avant d\'infliger des dgts magique, ';
        break;
      case 19:
        description += 'Aprs avoir inflig des dgts magique, ';
        break;
      case 20:
        description += 'Aprs de procurer un soin, ';
        break;
      case 21:
        description += 'Aprs avoir procur un soin, ';
        break;
      case 22:
        description += 'Avant la rcption d\'une action, ';
        break;
      case 23:
        description += 'Aprs avoir reu une action, ';
        break;
      case 24:
        description += 'Avant de recevoir une attaque, ';
        break;
      case 25:
        description += 'Aprs avoir reu une attaque, ';
        break;
      case 26:
        description += 'Avant de recevoir une competence, ';
        break;
      case 27:
        description += 'Aprs avoir reu une competence, ';
        break;
      case 28:
        description += 'Avant de recevoir des dgts, ';
        break;
      case 29:
        description += 'Aprs avoir reu des dgts, ';
        break;
      case 30:
        description += 'Avant de recevoir des dgts physiques, ';
        break;
      case 31:
        description += 'Aprs avoir reu des dgts physique, ';
        break;
      case 32:
        description += 'Avant de recevoir des dgts magiques, ';
        break;
      case 33:
        description += 'Aprs avoir reu des dgts magiques, ';
        break;
      case 34:
        description += 'Avant de recevoir des soins, ';
        break;
      case 35:
        description += 'Aprs avoir reu des soins, ';
        break;
      case 36:
        description += 'En dbut de tour, ';
        break;
      case 37:
        description += 'En fin de tour, ';
        break;

    }
    if (+this.actionType > 7 && +this.actionType < 35) {
      if (calcul > 0) {
        description += 'augmente ';
      } else if (calcul < 0) {
        description += 'diminue ';
      }
    }

    switch (+this.effectType) {
      case 1:
        description += 'les Degats de ';
        break;
      case 2:
        description += 'les Degats de ';
        break;
      case 3:
        description += 'les DegatsPhysique de ';
        break;
      case 4:
        description += 'les DegatsPhysique de ';
        break;
      case 5:
        description += 'les DegatsMagique de ';
        break;
      case 6:
        description += 'les DegatsMagique de ';
        break;
      case 7:
        description += 'la Force de ';
        break;
      case 8:
        description += 'l\'Agilite de ';
        break;
      case 9:
        description += 'l\'Intelligence de ';
        break;
      case 10:
        description += 'la Vitalite de ';
        break;
      case 11:
        description += 'les Pa de ';
        break;
      case 12:
        description += 'les PM de ';
        break;
      case 13:
        description += 'les Degats des sorts de ';
        break;
      case 14:
        description += 'les Degats des sorts de ';
        break;
      case 15:
        description += 'l\'Armure de ';
        break;
      case 16:
        description += 'l\'Armure de ';
        break;
      case 17:
        description += 'l\'Armure Magique de ';
        break;
      case 18:
        description += 'l\'Armure Magique de ';
        break;
      case 19:
        description += 'la rduction des dgts de ';
        break;
      case 20:
        description += 'la rduction des dgts de ';
        break;
      case 21:
        description += 'les Soins de ';
        break;
      case 22:
        description += 'les Soins de ';
        break;
      case 23:
        description += 'les Soins reus de ';
        break;
      case 24:
        description += 'les Soins reu de ';
        break;
      case 25:
        description += 'l\'IgnoreArmure de ';
        break;
      case 26:
        description += 'l\'IgnoreArmure de ';
        break;
      case 27:
        description += 'l\'IgnoreArmureMagique de ';
        break;
      case 28:
        description += 'l\'IgnoreArmureMagique de ';
        break;
      case 29:
        description += 'le Nombre d\'attaque de ';
        break;
      case 30:
        description += 'la RedirectionDegats de ';
        break;
      case 31:
        description += 'la RedirectionDegats de ';
        break;
      case 32:
        description += 'le RenovieDegats de ';
        break;
      case 33:
        description += 'le RenovieDegats de ';
        break;
      case 34:
        description += 'la Porte de ';
        break;

      case 35:
        description += 'inflige ';
        break;
      case 36:
        description += 'applique des dgts diffrs pour un montant de ';
        break;
      case 37:
        description += 'soigne de ';
        break;
      case 38:
        description += 'donne ';
        break;
    }

    description += '' + calcul;
    // TODO: Revoir l'organisation des effect type dans la BDD, pour avoir quelque chose de plus propre. Et permettre les % de stats.
    if (((+this.effectType % 2 === 0 && +this.effectType < 29) ||
      (+this.effectType % 2 === 1 && +this.effectType > 29 && +this.effectType < 34))
      && +this.effectType !== 8 && +this.effectType !== 9 && +this.effectType !== 10
      && +this.effectType !== 11 && +this.effectType !== 12 && +this.actionType > 6) {
      description += '%';
    }

    switch (+this.actionType) {
      case 1:
      case 2:
        description += ' points de dgts.';
        break;
      case 3:
      case 4:
      case 5:
        description += ' points de vie.';
        break;
      case 6:
        description += ' points de bouclier.';
        break;
    }
    if (+this.effectType === 35) {
      description += ' points de dgts.';
    } else if (+this.effectType === 38) {
      description += ' points de bouclier';
    }

    if (+this.actionType > 6) {
      if (+this.numberOfUse !== null && +this.numberOfUse !== 0) {
        description += ' pour ' + +this.numberOfUse + ' utilisation';
      } else if (+this.numberOfTurn && +this.numberOfTurn !== 0) {
        description += ' pour ' + +this.numberOfUse + ' tours';
      } else if (+this.numberOfFight && +this.numberOfFight !== 0) {
        description += ' pour ' + +this.numberOfUse + ' combats';
      }
    }
    return description;
  }

  private getActionTypePopoverBody(): string {
    /*
    let actionTypeList = "<ol>";
    for (const actionType of this.effectAddCompetenceEffectService.getActionType()) {
      actionTypeList += '<li>' + actionType.name + '</li>';
    }
    actionTypeList += "</ol>";
    return actionTypeList
    */
    return this.effectAddCompetenceEffectService.getTableActionTypeForPopover();
  }

  private getActionTypePopoverTitle(): string {
    return 'ActionType';
  }

  private getEffectTypePopoverBody(): string {
    /*
    let effectTypeList = "<ol>";
    for (const effectType of this.effectAddCompetenceEffectService.getEffectType()) {
      effectTypeList += '<li>' + effectType.name + '</li>';
    }
    effectTypeList += "</ol>";
    return effectTypeList
    */
    return this.effectAddCompetenceEffectService.getTableEffectTypeForPopover();
  }

  private getApplicationTypePopoverTitle(): string {
    return 'ApplicationType';
  }

  private getApplicationTypePopoverBody(): string {
    /*
    let effectTypeList = "<ol>";
    for (const effectType of this.effectAddCompetenceEffectService.getEffectType()) {
      effectTypeList += '<li>' + effectType.name + '</li>';
    }
    effectTypeList += "</ol>";
    return effectTypeList
    */
    return this.effectAddCompetenceEffectService.getTableApplicationTypeForPopover();
  }

  private getEffectTypePopoverTitle(): string {
    return 'EffectType';
  }

  private getTypeCalculToolTip(): string {
    return 'Type calcul dfinie le nombre de statistiques  prendre en compte.<br/>' +
      'La forme du calcul est : calcul1A + statistique1/calcul1B [+ calcul2A + statistique2/Calcul2B]';
  }

  private getNiveauRequisToolTip(): string {
    return 'Niveau que doit avoir la comptence pour que l\'effet puisse tre utilis';
  }

  private getAmplitudeToolTip(): string {
    return 'La puissance du d  lancer (4 pour dfinir un d 4). Le nombre de ds  lancer est  dfinir dans NombreAmplitude';
  }

  private getNombreAmplitudeToolTip(): string {
    return 'Le nombre de ds  lancer (3 pour lancer 3 ds identiques). La puissance du d  lancer est  dfinir dans Amplitude';
  }

  private getPourcentageToolTip(): string {
    return 'Indique si la valeur doit tre pris en tant que telle ou en tant que pourcentage  appliquer';
  }

  private getStatistiqueToolTip(): string {
    return 'Force / Agilit / Intelligence / Vitalit / Ressource sont les caracttitiques du personnage.<br/>' +
      'Niveau correspond au niveau de la compttence.';
  }

  private whatStatistique1Selected(ressource: string) {
    if (this.statistique1 !== undefined && this.statistique1 !== null) {
      return this.statistique1.toLowerCase().replace('', 'e') === ressource.toLowerCase().replace('', 'e');
    } else {
      return false;
    }
  }

  private noStatistique1Selected() {
    return this.statistique1 === undefined || this.statistique2 === null;
  }

  private whatStatistique2Selected(ressource: string) {
    if (this.statistique2 !== undefined && this.statistique2 !== null) {
      return this.statistique2.toLowerCase().replace('', 'e') === ressource.toLowerCase().replace('', 'e');
    } else {
      return false;
    }
  }

  private noStatistique2Selected() {
    return this.statistique2 === undefined || this.statistique2 === null;
  }

  private setValidateButtonText() {
    const button = document.getElementById('submissionEffectButton');
    if (+this.idCompetenceEffect === -1) {
      this.validationText = 'Ajouter l\'effet';
      button.className.replace('btn-danger', 'btn-warning');
      button.classList.remove('btn-danger');
      button.classList.add('btn-warning');
      button.style.width = '123px';

    } else {
      this.validationText = 'Valider les modifications';
      button.className.replace('btn-warning', 'btn-danger');
      button.classList.remove('btn-warning');
      button.classList.add('btn-danger');
      button.style.width = '198px';
    }
  }
}
