import { Injectable } from '@angular/core';
import {PersonnageIconComponent} from '../combat-session/personnage-icon/personnage-icon.component';
import {BASE_URL} from './rest.service';
import {Observable, Subscription} from 'rxjs';
import {map} from 'rxjs/operators';
import {HttpClient} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class EntitiesService {

  entities: PersonnageIconComponent[] = [];

  constructor() { }

  public getEntities(http: HttpClient, combatSession: number): PersonnageIconComponent[] {
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/combatEntitiesRest.php?id='
      + combatSession;

    const personnageIconObservable: Observable<PersonnageIconComponent[]> = http.get<PersonnageIconComponent[]>(baseUrlBis).pipe(
      // Adapt each item in the raw data array
      map((data: PersonnageIconComponent[]) => data.map(item => item)),
    );
    const personnageIconSuscription: Subscription = personnageIconObservable.subscribe(
      (value) => {
        this.entities = value;
        personnageIconSuscription.unsubscribe();
      },
      (error) => { console.log('Une erreur est survenue dans la suscription: ' + error); },
      () => {}
    );

    return this.entities;
  }
}
