import {Component, OnInit} from '@angular/core';
import {Statistiques} from '../../combat-session/personnage/statistiques';
import {BonusCombat} from '../../combat-session/personnage/BonusCombat';
import {StatistiquesService} from '../../service/statistiques.service';
import {BASE_URL} from '../../service/rest.service';
import {Observable, Subscription} from 'rxjs';
import {CompetenceAddCompetenceEffectComponent} from '../competence-add-competence-effect/competence-add-competence-effect.component';
import {map} from 'rxjs/operators';
import {HttpClient} from '@angular/common/http';
import {ActivatedRoute} from '@angular/router';
import {EffectAddCompetenceEffectService} from '../../service/effect-add-competence-effect.service';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-personnage-add-competence-effect',
  templateUrl: './personnage-add-competence-effect.component.html',
  styleUrls: ['./personnage-add-competence-effect.component.scss']
})
export class PersonnageAddCompetenceEffectComponent implements OnInit {

  public idPersonnage: number;
  public libelle: string;
  public niveau: number;
  public pa: number;
  public pm: number;
  public force: number;
  public agilite: number;
  public intelligence: number;
  public vitalite: number;
  public pdvActuel: number;
  public bouclier: number;
  public ressource: number;
  public typeRessource: string;
  public bonusForce: number;
  public bonusAgilite: number;
  public bonusIntelligence: number;
  public bonusVitalite: number;
  public bonusRessource: number;
  public bonusArmure: number;
  public bonusReussite: number;
  public icon: string;
  public statistiques: Statistiques;


  public competenceList: CompetenceAddCompetenceEffectComponent[] = [];

  constructor(private statistiquesService: StatistiquesService,
              private effectAddCompetenceEffectService: EffectAddCompetenceEffectService,
              private http: HttpClient, private route: ActivatedRoute, private modalService: NgbModal) {
    this.route.params.subscribe(params => {
      this.idPersonnage = params['idPersonnage'];
      if (this.idPersonnage !== undefined) {
        this.init();
        this.effectAddCompetenceEffectService.personnageAddEffectCompetenceComponent = this;
      }
    });
  }

  ngOnInit() {
  }

  public init() {
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/personnageRest.php?id=' + this.idPersonnage;
    const personnageAddCompetenceEffectComponentObservable: Observable<PersonnageAddCompetenceEffectComponent> =
      this.http.get<PersonnageAddCompetenceEffectComponent>(baseUrlBis).pipe(
        // Adapt each item in the raw data array
        map((item: PersonnageAddCompetenceEffectComponent) => this.adaptFromInside(item)),
      );
    const personnageAddCompetenceEffectComponentSuscription: Subscription = personnageAddCompetenceEffectComponentObservable.subscribe(
      (value) => {
        personnageAddCompetenceEffectComponentSuscription.unsubscribe();
      },
      (error) => {
        console.log('Une erreur est survenue dans la suscription: ' + error);
      },
      () => {
      }
    );
  }

  adaptFromInside(item: any) {
    this.idPersonnage = item.Id_Personnage;
    this.libelle = item.Libell;
    this.niveau = item.Niveau;
    this.pa = item.PA;
    this.pm = item.PM;
    this.force = item.Force;
    this.agilite = item.Agilite;
    this.intelligence = item.Intelligence;
    this.vitalite = item.Vitalite;
    this.pdvActuel = item.PDV_Actuel;
    this.bouclier = item.Bouclier;
    this.ressource = item.Ressource;
    this.typeRessource = item.Type_Ressource;
    this.icon = '../../assets/' + item.Libell + '/Icon.png';
    this.bonusForce = item.BonusForce;
    this.bonusAgilite = item.BonusAgilite;
    this.bonusIntelligence = item.BonusIntelligence;
    this.bonusVitalite = item.BonusVitalite;
    this.bonusRessource = item.BonusRessource;
    this.bonusArmure = item.BonusArmure;
    this.bonusReussite = item.BonusReussite;
    this.statistiques = new Statistiques(+this.idPersonnage,
      +0,
      +this.intelligence, +this.bonusIntelligence,
      +this.force, +this.bonusForce,
      +this.agilite, +this.bonusAgilite,
      +this.vitalite, +this.bonusVitalite,
      +this.ressource, +this.bonusRessource,
      +this.bonusArmure, +this.bonusArmure);
    this.statistiques.bonusCombat = null;
    this.statistiquesService.setStatistique(this.statistiques);
    return this;
  }

  public getCompetences() {
    this.competenceList = [];
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/competencesAddCompetenceEffectRest.php?id=' + this.idPersonnage;
    const competenceAddCompetenceEffectComponentObservable: Observable<CompetenceAddCompetenceEffectComponent[]> =
      this.http.get<CompetenceAddCompetenceEffectComponent[]>(baseUrlBis).pipe(
        // Adapt each item in the raw data array
        map((data: CompetenceAddCompetenceEffectComponent[]) => data.map(item => item)),
      );
    const competenceAddCompetenceEffectComponentSuscription: Subscription = competenceAddCompetenceEffectComponentObservable.subscribe(
      (value) => {
        for (const val of value) {
            const newCompetence = new CompetenceAddCompetenceEffectComponent(this.http, this.route,
              this.statistiquesService, this.effectAddCompetenceEffectService, this.modalService);
            newCompetence.adaptFromInside(val);
            if (+newCompetence.idCompetence !== 0) {
              console.log(newCompetence.idCompetence);
              this.competenceList.push(newCompetence);
            }
        }
        // this.competenceList = value;
        competenceAddCompetenceEffectComponentSuscription.unsubscribe();
      },
      (error) => {
        console.log('Une erreur est survenue dans la suscription: ' + error);
      },
      () => {
      }
    );
    return this.competenceList;
  }

}
