import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {JetDeLaveComponent} from './competences/jet-de-lave/jet-de-lave.component';
import {CombatSessionComponent} from './combatSession.component';
import {CanalisationComponent} from './competences/canalisation/canalisation.component';
import {BouleDeLaveComponent} from './competences/boule-de-lave/boule-de-lave.component';
import {JavelotDeLaveComponent} from './competences/javelot-de-lave/javelot-de-lave.component';


const routes: Routes = [
  {
    path: 'personnage/:id', component: CombatSessionComponent,
    children: [
      {path: '175', component: JetDeLaveComponent, outlet: 'Competence'},
      {path: '185', component: CanalisationComponent, outlet: 'Competence'},
      {path: '176', component: BouleDeLaveComponent, outlet: 'Competence'},
      {path: '177', component: JavelotDeLaveComponent, outlet: 'Competence'}
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CombatSessionRoutingModule { }
