import {Injectable} from '@angular/core';
import {map} from 'rxjs/operators';
import {Subscription} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {BASE_URL} from '../../service/rest.service';

@Injectable({
  providedIn: 'root'
})
export class BonusCombat {

  public DegatsFlat: number;
  public DegatsPourcentage: number;
  public DegatsPhysiqueFlat: number;
  public DegatsPhysiquePourcentage: number;
  public DegatsMagiqueFlat: number;
  public DegatsMagiquePourcentage: number;
  public Force: number;
  public Agilite: number;
  public Intelligence: number;
  public Vitalite: number;
  public PA: number;
  public PM: number;
  public SortFlat: number;
  public SortPourcentage: number;
  public ArmureFlat: number;
  public ArmurePourcentage: number;
  public ArmureMagiqueFlat: number;
  public ArmureMagiquePourcentage: number;
  public ReductionDegatsFlat: number;
  public ReductionDegatsPourcentage: number;
  public SoinFlat: number;
  public SoinPourcentage: number;
  public SoinRecuFlat: number;
  public SoinRecuPourcentage: number;
  public IgnoreArmureFlat: number;
  public IgnoreArmurePourcentage: number;
  public IgnoreArmureMagiqueFlat: number;
  public IgnoreArmureMagiquePourcentage: number;
  public AugmenteNombreAttaque: number;
  public RedirectionDegatFlat: number;
  public RedirectionDegatPourcentage: number;
  public RenvoieDegatFlat: number;
  public RenvoieDegatPourcentage: number;
  public Portee: number;
  public Degat: number;
  public DegatDiffere: number;
  public Soin: number;
  public Shield: number;
  public DiffererDegatsFlatToTheEndOfEffect: number;
  public DiffererDegatsPourcentageToTheEndOfEffect: number;
  public DiffererDegatsFlat: number;
  public DiffererDegatsPourcentage: number;

  // tslint:disable-next-line:variable-name
  constructor(public Id_Personnage,
              private http: HttpClient) { this.loadItSelf(); }

  loadItSelf() {
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/personnageBonusCombat.php?id=' + this.Id_Personnage;
    // console.log('Rcupration personnage');
    const bonusCombatObservable = this.http.get<BonusCombat>(baseUrlBis).pipe(
      // Adapt each item in the raw data array
      map((item: BonusCombat) => this.adaptFromInside(item)),
    );
    const bonusCombatSuscription: Subscription = bonusCombatObservable.subscribe(
      () => {
        bonusCombatSuscription.unsubscribe();
      },
      (error) => { console.log('Une erreur est survenue dans la suscription: ' + error); },
      () => {}
    );
  }

  adaptFromInside(item: any) {
    this.DegatsFlat = item.DegatsFlat;
    this.DegatsPourcentage = item.DegatsPourcentage;
    this.DegatsPhysiqueFlat = item.DegatsPhysiqueFlat;
    this.DegatsPhysiquePourcentage = item.DegatsPhysiquePourcentage;
    this.DegatsMagiqueFlat = item.DegatsMagiqueFlat;
    this.DegatsMagiquePourcentage = item.DegatsMagiquePourcentage;
    this.Force = item.Force;
    this.Agilite = item.Agilite;
    this.Intelligence = item.Intelligence;
    this.Vitalite = item.Vitalite;
    this.PA = item.PA;
    this.PM = item.PM;
    this.SortFlat = item.SortFlat;
    this.SortPourcentage = item.SortPourcentage;
    this.ArmureFlat = item.ArmureFlat;
    this.ArmurePourcentage = item.ArmurePourcentage;
    this.ArmureMagiqueFlat = item.ArmureMagiqueFlat;
    this.ArmureMagiquePourcentage = item.ArmureMagiquePourcentage;
    this.ReductionDegatsFlat = item.ReductionDegatsFlat;
    this.ReductionDegatsPourcentage = item.ReductionDegatsPourcentage;
    this.SoinFlat = item.SoinFlat;
    this.SoinPourcentage = item.SoinPourcentage;
    this.SoinRecuFlat = item.SoinRecuFlat;
    this.SoinRecuPourcentage = item.SoinRecuPourcentage;
    this.IgnoreArmureFlat = item.IgnoreArmureFlat;
    this.IgnoreArmurePourcentage = item.IgnoreArmurePourcentage;
    this.IgnoreArmureMagiqueFlat = item.IgnoreArmureMagiqueFlat;
    this.IgnoreArmureMagiquePourcentage = item.IgnoreArmureMagiquePourcentage;
    this.AugmenteNombreAttaque = item.AugmenteNombreAttaque;
    this.RedirectionDegatFlat = item.RedirectionDegatFlat;
    this.RedirectionDegatPourcentage = item.RedirectionDegatPourcentage;
    this.RenvoieDegatFlat = item.RenvoieDegatFlat;
    this.RenvoieDegatPourcentage = item.RenvoieDegatPourcentage;
    this.Portee = item.Portee;
    this.Degat = item.Degat;
    this.DegatDiffere = item.DegatDiffere;
    this.Soin = item.Soin;
    this.Shield = item.Shield;
    this.DiffererDegatsFlatToTheEndOfEffect = item.DiffererDegatsFlatToTheEndOfEffect;
    this.DiffererDegatsPourcentageToTheEndOfEffect = item.DiffererDegatsPourcentageToTheEndOfEffect;
    this.DiffererDegatsFlat = item.DiffererDegatsFlat;
    this.DiffererDegatsPourcentage = item.DiffererDegatsPourcentage;
  }

  getDegatsPhysiqueWithBonus(value: number) { return (this.DegatsPhysiqueFlat + value) * this.DegatsPhysiquePourcentage; }
  getDegatsMagiqueWithBonus(value: number) { return (this.DegatsMagiqueFlat + value) * this.DegatsMagiquePourcentage; }
  getForce(value: number) { return this.Force; }
  getAgilite(value: number) { return this.Agilite; }
  getIntelligence(value: number) { return this.Intelligence; }
  getVitalite(value: number) { return this.Vitalite; }
  getPA() { return this.PA; }
  getPM() { return this.PM; }
  getSortWithBonus(value: number) { return (this.SortFlat + value) * this.SortPourcentage; }
  getArmureWithBonus(value: number) { return (this.ArmureFlat + value) * this.ArmurePourcentage; }
  getArmureMagiqueWithBonus(value: number) { return (this.ArmureMagiqueFlat + value) * this.ArmureMagiquePourcentage; }
  getDegatsWithBonus(value: number) { return (this.DegatsFlat + value) * this.DegatsPourcentage; }
  getSoinWithBonus(value: number) { return (this.SoinFlat + value) * this.SoinPourcentage; }
  getPorteeWithBonus(value: number) { return (this.Portee + value) * this.Portee; }

}
