import {Component, Input, OnInit} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {map} from 'rxjs/operators';
import {Subscription} from 'rxjs';
import {BASE_URL} from '../../service/rest.service';

@Component({
  selector: 'app-personnage-icon',
  templateUrl: './personnage-icon.component.html',
  styleUrls: ['./personnage-icon.component.scss']
})
export class PersonnageIconComponent implements OnInit {

  // tslint:disable:variable-name
  @Input() public Id_Personnage: number;
  public Libelle: string;
  public Image: string;
  public iconImage: string;
  public Niveau: number;
  public DegatsRecus;
  public Bouclier;
  public team: number;
  public initiative: number;
  public isAlive: boolean;

  constructor(private http: HttpClient) { }

  ngOnInit() {
  }

  public loadItSelf() {
    // console.log(this.Id_Competence);
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/personnageIconAngularRest.php?id='
      + this.Id_Personnage;
    const competenceIconObservable = this.http.get<PersonnageIconComponent>(baseUrlBis).pipe(
      // Adapt each item in the raw data array
      map((item: PersonnageIconComponent) => this.adaptFromInside(item)),
    );
    const competenceIconSubsciption: Subscription = competenceIconObservable.subscribe(
      () => {
        competenceIconSubsciption.unsubscribe();
      },
      (error) => {
        console.log('Une erreur est survenue dans la suscription de la competence: ' + error);
      },
      () => {
      }
    );
  }

  adaptFromInside(item: any) {
    this.Id_Personnage = item.Id_Personnage;
    this.Libelle = item.Libell;
    this.Image = item.Image;
    this.iconImage = item.iconImage;
    this.Niveau = item.Niveau;
    this.team = item.team;
    this.initiative = item.initiative;
    this.isAlive = item.isAlive;
    this.DegatsRecus = item.DegatsRecus;
    this.Bouclier = item.Bouclier;
  }

}
