import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {AddCompetenceEffectComponent} from './add-competence-effect.component';
import {PersonnageAddCompetenceEffectComponent} from '../personnage-add-competence-effect/personnage-add-competence-effect.component';
import {CompetenceAddCompetenceEffectComponent} from '../competence-add-competence-effect/competence-add-competence-effect.component';
import {EffectAddCompetenceEffectComponent} from '../effect-add-competence-effect/effect-add-competence-effect.component';


const routes: Routes = [
  {
    path: 'addEffect', component: AddCompetenceEffectComponent,
    children: [
      {path: ':idPersonnage', component: PersonnageAddCompetenceEffectComponent, outlet: 'personnageAddCompetenceEffect'},
      {
        path: ':idCompetence', component: CompetenceAddCompetenceEffectComponent, outlet: 'competenceAddCompetenceEffect',
        children: [
          {path: ':idEffect', component: EffectAddCompetenceEffectComponent, outlet: 'effectAddCompetenceEffect'},
        ]
      }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AddCompetenceEffectRoutingModule {
}
