import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {ChoixPersonnageComponent} from './combat-session/choix-personnage/choix-personnage.component';
import {AddCompetenceEffectComponent} from './addCompetenceEffect/add-competence-effect/add-competence-effect.component';


export const routes: Routes = [
  {path: 'choixPersonnages', component: ChoixPersonnageComponent},
  {path: 'choixPersonnages', loadChildren: () => import('./combat-session/combat-session.module').then(m => m.CombatSessionModule)},
  {path: '', redirectTo: 'choixPersonnages', pathMatch: 'full'}
  ];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
