  public loadItSelf() {
    // console.log(this.Id_Competence);
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/competenceAngularRest.php?id=' + this.Id_Competence;
    const competenceObservable = this.http.get<CompetenceComponent>(baseUrlBis).pipe(
      // Adapt each item in the raw data array
      map((item: CompetenceComponent) => this.adaptFromInside(item)),
    );
    const competenceSuscription: Subscription = competenceObservable.subscribe(
      () => {
        competenceSuscription.unsubscribe();
      },
      (error) => {
        console.log('Une erreur est survenue dans la suscription de la competence: ' + error);
      },
      () => {
      }
    );
  }

  public loadItSelfWithEffect() {
    // console.log(this.Id_Competence);
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/competenceWithEffectsAngularRest.php?id=' + this.Id_Competence;
    const competenceObservable = this.http.get<CompetenceComponent>(baseUrlBis).pipe(
      // Adapt each item in the raw data array
      map((item: CompetenceComponent) => this.adaptFromInsideWithEffects(item)),
    );
    const competenceSuscription: Subscription = competenceObservable.subscribe(
      () => {
        competenceSuscription.unsubscribe();
      },
      (error) => {
        console.log('Une erreur est survenue dans la suscription de la competence: ' + error);
      },
      () => {
      }
    );
  }

