import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {BASE_URL} from '../../service/rest.service';
import {Observable, Subscription} from 'rxjs';
import {map} from 'rxjs/operators';
import {EffectAddCompetenceEffectComponent} from '../effect-add-competence-effect/effect-add-competence-effect.component';
import {ActivatedRoute} from '@angular/router';
import {StatistiquesService} from '../../service/statistiques.service';
import {EffectAddCompetenceEffectService} from '../../service/effect-add-competence-effect.service';
import { faMinusCircle } from '@fortawesome/free-solid-svg-icons';
import {ModalDismissReasons, NgbModal, NgbModalOptions} from '@ng-bootstrap/ng-bootstrap';
import {NgbdModalConfirm, NgbdModalConfirmAutofocus} from '../../modal-focus/modal-focus';

const MODALS = {
  focusFirst: NgbdModalConfirm,
  autofocus: NgbdModalConfirmAutofocus};

@Component({
  selector: 'app-competence-add-competence-effect',
  templateUrl: './competence-add-competence-effect.component.html',
  styleUrls: ['./competence-add-competence-effect.component.scss']
})
export class CompetenceAddCompetenceEffectComponent implements OnInit {

  faMinusCircle =  faMinusCircle;

  modalOptions: NgbModalOptions;

  public effectList: EffectAddCompetenceEffectComponent[] = [];
  public idCompetence: number;
  public libelle: string;
  public image: string;
  public idEffect: number;
  @Output() effectSelectedEmitter = new EventEmitter<number>();
  closeResult: string;

  constructor(private http: HttpClient, private route: ActivatedRoute, private statistiquesService: StatistiquesService,
              private effectAddCompetenceEffectService: EffectAddCompetenceEffectService, private modalService: NgbModal) {
    this.route.params.subscribe( params => {
      this.idCompetence = params.idCompetence;
      if (this.idCompetence !== undefined) {
        this.init();
        this.getEffects();
        this.idEffect = undefined;
        this.effectAddCompetenceEffectService.competenceAddEffectCompetenceComponent = this;
      }
    });

    this.modalOptions = {
      backdrop: 'static',
      backdropClass: 'customBackdrop'
    };
  }

  public init() {
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/competenceAddCompetenceEffectRest.php?id=' + this.idCompetence;
    const competenceAddCompetenceEffectComponentObservable = this.http.get<CompetenceAddCompetenceEffectComponent>(baseUrlBis).pipe(
      // Adapt each item in the raw data array
      map((item: CompetenceAddCompetenceEffectComponent) => this.adaptFromInside(item)),
    );
    const competenceAddCompetenceEffectComponentSuscription: Subscription = competenceAddCompetenceEffectComponentObservable.subscribe(
      () => {
        competenceAddCompetenceEffectComponentSuscription.unsubscribe();
      },
      (error) => {
        console.log('Une erreur est survenue dans la suscription: ' + error);
      },
      () => {
      }
    );
  }

  open(name: string, idCompetenceEffect: number) {
    const modalRef = this.modalService.open(MODALS[name]);
    modalRef.componentInstance.modalTitle = 'CompetenceEffect deletion';
    modalRef.componentInstance.modalContent = '<p>' +
      '<strong>Es-tu sr de vouloir supprimer cet effet de  <span class="text-primary">"' + this.libelle + '"</span> ?</strong>' +
      '</p>    ' +
      '<p>Toute information associe  cet effet de comptence sera supprime de manire permanente.   ' +
      ' <span class="text-danger">Cette opration ne pourra tre annule.</span>    ' +
      '</p>';

    modalRef.result.then(
      () => {
        this.effectAddCompetenceEffectService.deleteEffect(idCompetenceEffect);
    }, (reason => {
        this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      }));
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

  ngOnInit() {
  }

  notifyEffectSelection(idEffect: number) {
    this.idEffect = idEffect;
    this.effectSelectedEmitter.emit(this.idEffect);
  }

  public getEffects() {
    this.effectList = [];
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/effectsAddCompetenceEffectRest.php?id=' + this.idCompetence;
    const effectAddCompetenceEffectComponentObservable: Observable<EffectAddCompetenceEffectComponent[]> =
      this.http.get<EffectAddCompetenceEffectComponent[]>(baseUrlBis).pipe(
        // Adapt each item in the raw data array
        map((data: EffectAddCompetenceEffectComponent[]) => data.map(item => item)),
      );
    const effectAddCompetenceEffectComponentSuscription: Subscription = effectAddCompetenceEffectComponentObservable.subscribe(
      (value) => {
        for (const effect of value) {
          const newEffect = new EffectAddCompetenceEffectComponent(this.http, this.route, this.statistiquesService,
            this.effectAddCompetenceEffectService);
          newEffect.idCompetenceEffect = effect.idCompetenceEffect;
          newEffect.adaptFromInside(effect);
          this.effectList.push(newEffect);
        }
        // this.effectList = value;
        effectAddCompetenceEffectComponentSuscription.unsubscribe();
      },
      (error) => { console.log('Une erreur est survenue dans la suscription: ' + error); },
      () => {}
    );
    return this.effectList;
  }

  adaptFromInside(item: any) {
    this.idCompetence = item.Id_Competence;
    this.libelle = item.Libell;
    this.image = item.Image;
  }

  public getImage(): string {
    return '../../assets/' + this.image;
  }

  public updateCompetence() {
    if (this.idEffect === -1) {

    } else if (this.idEffect > 0) {

    }
  }
}
