import {Injectable} from '@angular/core';
import {HttpClient, HttpParams} from '@angular/common/http';
import {BASE_URL} from './rest.service';
import {Observable, Subscription} from 'rxjs';
import {map} from 'rxjs/operators';
import {TypeEffect} from './type-effect';
import {Tab} from './tab';
import {EffectAddCompetenceEffectComponent} from '../addCompetenceEffect/effect-add-competence-effect/effect-add-competence-effect.component';
import {CompetenceAddCompetenceEffectComponent} from '../addCompetenceEffect/competence-add-competence-effect/competence-add-competence-effect.component';
import {PersonnageAddCompetenceEffectComponent} from '../addCompetenceEffect/personnage-add-competence-effect/personnage-add-competence-effect.component';
import {AddCompetenceEffectComponent} from '../addCompetenceEffect/add-competence-effect/add-competence-effect.component';

@Injectable({
  providedIn: 'root'
})
export class EffectAddCompetenceEffectService {

  public ActionType: TypeEffect[] = [];
  public EffectType: TypeEffect[] = [];
  public ApplicationType: TypeEffect[] = [];

  public effectAddEffectCompetenceComponent: EffectAddCompetenceEffectComponent;
  public competenceAddEffectCompetenceComponent: CompetenceAddCompetenceEffectComponent;
  public personnageAddEffectCompetenceComponent: PersonnageAddCompetenceEffectComponent;
  public addEffectCompetenceComponent: AddCompetenceEffectComponent;

  constructor(private http: HttpClient) {
  }

  public getActionType(): TypeEffect[] {
    return this.ActionType;
  }

  public getEffectType(): TypeEffect[] {
    return this.EffectType;
  }

  public setActionType(actionType: TypeEffect[]) {
    this.ActionType = actionType;
  }

  public setEffectType(effectType: TypeEffect[]) {
    this.EffectType = effectType;
  }

  public loadActionType(http: HttpClient) {
    this.ActionType = [];
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/actionTypeNames.php';
    const actionTypeObservable: Observable<TypeEffect[]> =
      http.get<TypeEffect[]>(baseUrlBis).pipe(
        // Adapt each item in the raw data array
        map((data: TypeEffect[]) => data.map(item => item)),
      );
    const actionTypeSuscription: Subscription = actionTypeObservable.subscribe(
      (value) => {
        for (const val of value) {
          const newEffect = new TypeEffect();
          newEffect.adaptFromInside(val);
          this.ActionType.push(newEffect);
        }
        actionTypeSuscription.unsubscribe();
      },
      (error) => {
        console.log('Une erreur est survenue dans la suscription: ' + error);
      },
      () => {
      }
    );
  }

  public loadEffectType(http: HttpClient) {
    this.EffectType = [];
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/effectTypeNames.php';
    const effectTypeObservable: Observable<TypeEffect[]> =
      http.get<TypeEffect[]>(baseUrlBis).pipe(
        // Adapt each item in the raw data array
        map((data: TypeEffect[]) => data.map(item => item)),
      );
    const effectTypeSuscription: Subscription = effectTypeObservable.subscribe(
      (value) => {
        for (const val of value) {
          const newEffect = new TypeEffect();
          newEffect.adaptFromInside(val);
          this.EffectType.push(newEffect);
        }
        effectTypeSuscription.unsubscribe();
      },
      (error) => {
        console.log('Une erreur est survenue dans la suscription: ' + error);
      },
      () => {
      }
    );
  }

  public loadApplicationType(http: HttpClient) {
    this.ApplicationType = [];
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/applicationTypeNames.php';
    const applicationTypeObservable: Observable<TypeEffect[]> =
      http.get<TypeEffect[]>(baseUrlBis).pipe(
        // Adapt each item in the raw data array
        map((data: TypeEffect[]) => data.map(item => item)),
      );
    const applicationTypeSubscription: Subscription = applicationTypeObservable.subscribe(
      (value) => {
        for (const val of value) {
          const newEffect = new TypeEffect();
          newEffect.adaptFromInside(val);
          this.ApplicationType.push(newEffect);
        }
        applicationTypeSubscription.unsubscribe();
      },
      (error) => {
        console.log('Une erreur est survenue dans la suscription: ' + error);
      },
      () => {
      }
    );
  }

  public loadService(http: HttpClient) {
    this.loadActionType(http);
    this.loadEffectType(http);
    this.loadApplicationType(http);
  }

  public getTableActionTypeForPopover(): string {

    const tab = new Tab();
    tab.addTabMenuItem('Base');
    let base = '';
    tab.addTabMenuItem('Effets Avant Lancement');
    let effetsAvantLancement = '';
    tab.addTabMenuItem('Effets Aprs Lancement');
    let effetsApresLancement = '';
    tab.addTabMenuItem('Effets Avant Reception');
    let effetsAvantReception = '';
    tab.addTabMenuItem('Effets Aprs Reception');
    let effetsApresReception = '';
    tab.addTabMenuItem('Autres Effets');
    let autresEffets = '';

    for (let index = 0; index < this.ActionType.length; index++) {
      if (index < 6) {
        base += this.ActionType[index].idType + ' : ' + this.ActionType[index].name + '<br/>';
      } else if (index > 6 && index < 21) {
        if (index % 2 === 1) {
          effetsAvantLancement += this.ActionType[index].idType + ' : ' + this.ActionType[index].name + '<br/>';
        } else {
          effetsApresLancement += this.ActionType[index].idType + ' : ' + this.ActionType[index].name + '<br/>';
        }
      } else if (index > 20 && index < 35) {
        if (index % 2 === 1) {
          effetsAvantReception += this.ActionType[index].idType + ' : ' + this.ActionType[index].name + '<br/>';
        } else {
          effetsApresReception += this.ActionType[index].idType + ' : ' + this.ActionType[index].name + '<br/>';
        }
      } else if (index > 34 || index === 6) {
        autresEffets += this.ActionType[index].idType + ' : ' + this.ActionType[index].name + '<br/>';
      }
    }

    tab.addTabContentItem(base);
    tab.addTabContentItem(effetsAvantLancement);
    tab.addTabContentItem(effetsApresLancement);
    tab.addTabContentItem(effetsAvantReception);
    tab.addTabContentItem(effetsApresReception);
    tab.addTabContentItem(autresEffets);

    // console.log(tab.displayTab());
    return tab.displayTab();
  }

  public getTableEffectTypeForPopover(): string {

    const tab = new Tab();
    tab.addTabMenuItem('Dgts');
    let degats = '';
    tab.addTabMenuItem('Statistiques');
    let statistiques = '';
    tab.addTabMenuItem('Rsistance');
    let resistance = '';
    tab.addTabMenuItem('Soin');
    let soin = '';
    tab.addTabMenuItem('Effets Directs');
    let effetsDirects = '';
    tab.addTabMenuItem('Autres');
    let autres = '';

    for (let index = 0; index < this.EffectType.length; index++) {
      if (index < 6) { // index est dcal de 1. Il faut prendre index-1 pour avoir le bon numro
        degats += this.EffectType[index].idType + ' : ' + this.EffectType[index].name + '<br/>';
      } else if ((index > 5 && index < 12) || index === 33) {
        statistiques += this.EffectType[index].idType + ' : ' + this.EffectType[index].name + '<br/>';
      } else if (index > 13 && index < 20) {
        resistance += this.EffectType[index].idType + ' : ' + this.EffectType[index].name + '<br/>';
      } else if (index > 19 && index < 24) {
        soin += this.EffectType[index].idType + ' : ' + this.EffectType[index].name + '<br/>';
      } else if ((index > 23 && index < 33) || index === 12 || index === 13) {
        autres += this.EffectType[index].idType + ' : ' + this.EffectType[index].name + '<br/>';
      } else if (index > 32) {
        effetsDirects += this.EffectType[index].idType + ' : ' + this.EffectType[index].name + '<br/>';
      }
    }

    tab.addTabContentItem(degats);
    tab.addTabContentItem(statistiques);
    tab.addTabContentItem(resistance);
    tab.addTabContentItem(soin);
    tab.addTabContentItem(effetsDirects);
    tab.addTabContentItem(autres);

    // console.log(tab.displayTab());
    return tab.displayTab();
  }

  public getTableApplicationTypeForPopover(): string {

    const tab = new Tab();
    tab.addTabMenuItem('Cible unique');
    let cibleUnique = '';
    tab.addTabMenuItem('Effet de Zone');
    let effetZone = '';
    tab.addTabMenuItem('Effet sur Prcdente cible');
    let effetPrecedenteCible = '';
    tab.addTabMenuItem('Autres');
    let autres = '';


    for (let index = 0; index < this.ApplicationType.length; index++) {
      if (index === 0 || index === 4 || index === 5) { // index est dcal de 1. Il faut prendre index-1 pour avoir le bon numro
        autres += this.ApplicationType[index].idType + ' : ' + this.ApplicationType[index].name + '<br/>';
      } else if ((index > 5 && index < 12) || index === 1) {
        cibleUnique += this.ApplicationType[index].idType + ' : ' + this.ApplicationType[index].name + '<br/>';
      } else if ((index > 11 && index < 18)  || index === 2) {
        effetZone += this.ApplicationType[index].idType + ' : ' + this.ApplicationType[index].name + '<br/>';
      } else if ((index > 17 && index < 22)  || index === 3) {
        effetPrecedenteCible += this.ApplicationType[index].idType + ' : ' + this.ApplicationType[index].name + '<br/>';
      }
    }

    tab.addTabContentItem(cibleUnique);
    tab.addTabContentItem(effetZone);
    tab.addTabContentItem(effetPrecedenteCible);
    tab.addTabContentItem(autres);

    // console.log(tab.displayTab());
    return tab.displayTab();
  }

  public updateOrCreateCompetenceEffect() {

    const values = {
      idCompetenceEffect: undefined,
      effectOrder: undefined,
      idCompetence: undefined,
      effectType: undefined,
      actionType: undefined,
      niveauRequis: undefined,
      typeCalcul: undefined,
      calcul1A: undefined,
      calcul1B: undefined,
      calcul2A: undefined,
      calcul2B: undefined,
      amplitude: undefined,
      nombreAmplitude: undefined,
      statistique1: undefined,
      statistique2: undefined,
      pourcentage: undefined,
      numberOfUse: undefined,
      numberOfTurn: undefined,
      numberOfFight: undefined,
    };
    // tslint:disable:max-line-length
    values.idCompetenceEffect = +this.competenceAddEffectCompetenceComponent.idEffect;
    values.effectOrder = +(document.getElementById('validationEffectOrder') as HTMLInputElement).value;
    values.idCompetence = +this.competenceAddEffectCompetenceComponent.idCompetence;
    values.effectType = document.getElementById('validationEffectType') === null ? null : +(document.getElementById('validationEffectType') as HTMLInputElement).value;
    values.actionType = +(document.getElementById('validationActionType') as HTMLInputElement).value;
    values.niveauRequis = +(document.getElementById('validationNiveauRequis') as HTMLInputElement).value;
    values.typeCalcul = +(document.getElementById('validationTypeCalcul') as HTMLInputElement).value;
    values.calcul1A = +(document.getElementById('validationCalcul1A') as HTMLInputElement).value;
    values.calcul1B = +(document.getElementById('validationCalcul1B') as HTMLInputElement).value;
    values.calcul2A = document.getElementById('validationCalcul2A') === null ? null : +(document.getElementById('validationCalcul2A') as HTMLInputElement).value;
    values.calcul2B = document.getElementById('validationCalcul2B') === null ? null : +(document.getElementById('validationCalcul2B') as HTMLInputElement).value;
    values.amplitude = +(document.getElementById('validationAmplitude') as HTMLInputElement).value;
    values.nombreAmplitude = +(document.getElementById('validationNombreAmplitude') as HTMLInputElement).value;
    values.statistique1 = (document.getElementById('validationStatistique1') as HTMLInputElement).value;
    values.statistique2 = document.getElementById('validationStatistique2') === null ? null : (document.getElementById('validationStatistique2') as HTMLInputElement).value;
    values.pourcentage = (document.getElementById('validationPoucentage') as HTMLInputElement).checked ? 1 : 0;
    values.numberOfUse = document.getElementById('validationNumberOfUse') === null ? null : +(document.getElementById('validationNumberOfUse') as HTMLInputElement).value;
    values.numberOfTurn = document.getElementById('validationNumberOfTurn') === null ? null : +(document.getElementById('validationNumberOfTurn') as HTMLInputElement).value;
    values.numberOfFight = document.getElementById('validationNumberOfFight') === null ? null : +(document.getElementById('validationNumberOfFight') as HTMLInputElement).value;


    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/addCompetenceEffect.php';
    const params = new HttpParams().set('EffectCompetence', JSON.stringify(values));
    // console.log(JSON.stringify(values));
    // console.log(params);

    /* PUT is not working. Only POST don't rise error ... */
    // const requestType = this.ActionType === 5 ? 'POST' :  'PUT';

    this.http.request('POST', baseUrlBis, {responseType: 'text', params}).toPromise()
      .then((data: any) => {
        this.competenceAddEffectCompetenceComponent.getEffects();
      });
  }

  public deleteEffect(idCompetenceEffect: number) {
    const values = {idCompetenceEffect: undefined};
    values.idCompetenceEffect = idCompetenceEffect;

    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/addCompetenceEffect.php';
    const params = new HttpParams().set('EffectCompetence', JSON.stringify(values));
    console.log(JSON.stringify(values));
    console.log(params);

    this.http.request('DELETE', baseUrlBis, {responseType: 'text', params}).toPromise()
      .then((data: any) => {
        console.log(data);
        this.competenceAddEffectCompetenceComponent.getEffects();
      });
  }

  public addCompetenceToCharacter(competenceName: string) {

    const values = {competenceName: undefined, idPersonnage: undefined};
    values.competenceName = competenceName;
    values.idPersonnage = this.personnageAddEffectCompetenceComponent.idPersonnage;
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/addCompetence.php';
    const params = new HttpParams().set('Competence', JSON.stringify(values));
    console.log(JSON.stringify(values));
    console.log(params);

    /* PUT is not working. Only POST don't rise error ... */
    // const requestType = this.ActionType === 5 ? 'POST' :  'PUT';

    return this.http.request('PUT', baseUrlBis, {responseType: 'text', params}).toPromise();
  }

  deleteCompetence(idCompetence: number, idPersonnage: number) {
    const values = {idCompetence: undefined, idPersonnage: undefined};
    values.idCompetence = idCompetence;
    values.idPersonnage = idPersonnage;

    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/addCompetence.php';
    const params = new HttpParams().set('Competence', JSON.stringify(values));
    console.log(JSON.stringify(values));
    console.log(params);

    this.http.request('DELETE', baseUrlBis, {responseType: 'text', params}).toPromise()
      .then((data: any) => {
        console.log(data);
        this.addEffectCompetenceComponent.reloadCompetences();
      });
  }
}
