import {
  Component,
  ComponentFactoryResolver,
  Input,
  OnInit, Renderer2,
  ViewContainerRef,
  OnDestroy,
} from '@angular/core';
import {Observable, Subscription} from 'rxjs';
import {map} from 'rxjs/operators';
import {HttpClient} from '@angular/common/http';
import {Statistiques} from './statistiques';
import {ModalService} from '../../_modal';
import {BonusCombat} from './BonusCombat';
import {StatistiquesService} from '../../service/statistiques.service';
// import {CompetenceIconComponent} from '../competence-icon/competence-icon.component';
import {BASE_URL} from '../../service/rest.service';
import {EntitiesService} from '../../service/entities.service';
import {PersonnageIconComponent} from '../personnage-icon/personnage-icon.component';

@Component({
  selector: 'app-personnage',
  templateUrl: './personnage.component.html',
  styleUrls: ['./personnage.component.scss']
})
export class PersonnageComponent implements OnInit, OnDestroy {
  // tslint:disable:variable-name
  @Input() Id_Personnage: number;
  public Libelle: number;
  public Niveau: number;
  public PA: number;
  public PA_Actuel: number;
  public PM: number;
  public PM_Actuel: number;
  public Force: number;
  public Agilite: number;
  public Intelligence: number;
  public Vitalite: number;
  public PDV_Actuel: number;
  public Bouclier: number;
  public Ressource: number;
  public Ressource_Actuelle: number;
  public Type_Ressource: string;
  public Reussite: number;
  public Charisme: number;
  public Marchandage: number;
  public Intimidation: number;
  public Chance: number;
  public Inventaire: string;
  public Image: string;
  public Ordre_Colorimetrique: number;
  public BonusForce: number;
  public BonusAgilite: number;
  public BonusIntelligence: number;
  public BonusVitalite: number;
  public BonusRessource: number;
  public BonusArmure: number;
  public BonusReussite: number;
  public CompetencesID: number[];
  // public CompetencesIcons: CompetenceIconComponent[] = [];
  public Statistiques: Statistiques;
  public team = 0;
  public combatSession = 1;
  entities: PersonnageIconComponent[] = [];

  constructor(private http: HttpClient,
              private vcRef: ViewContainerRef,
              private resolver: ComponentFactoryResolver,
              private componentFactoryResolver: ComponentFactoryResolver,
              private renderer: Renderer2,
              private modalService: ModalService,
              private statistiquesService: StatistiquesService,
              private entitiesService: EntitiesService) { }


  ngOnInit() {
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/personnageRest.php?id=' + this.Id_Personnage;
    // console.log('Rcupration personnage');
    const personnageObservable = this.http.get<PersonnageComponent>(baseUrlBis).pipe(
      // Adapt each item in the raw data array
      map((item: PersonnageComponent) => this.adaptFromInside(item)),
    );
    const personnageSuscription: Subscription = personnageObservable.subscribe(
      () => {
        personnageSuscription.unsubscribe();
        this.getCompetencesID();
      },
      (error) => { console.log('Une erreur est survenue dans la suscription: ' + error); },
      () => {}
    );
  }

  ngOnDestroy() {
  }

  adaptFromInside(item: any) {
    this.Id_Personnage = item.Id_Personnage;
    this.Libelle = item.Libelle;
    this.Niveau = item.Niveau;
    this.PA = item.PA;
    this.PA_Actuel = item.PA_Actuel;
    this.PM = item.PM;
    this.PM_Actuel = item.PM_Actuel;
    this.Force = item.Force;
    this.Agilite = item.Agilite;
    this.Intelligence = item.Intelligence;
    this.Vitalite = item.Vitalite;
    this.PDV_Actuel = item.PDV_Actuel;
    this.Bouclier = item.Bouclier;
    this.Ressource = item.Ressource;
    this.Ressource_Actuelle = item.Ressource_Actuelle;
    this.Type_Ressource = item.Type_Ressource;
    this.Reussite = item.Reussite;
    this.Charisme = item.Charisme;
    this.Marchandage = item.Marchandage;
    this.Intimidation = item.Intimidation;
    this.Chance = item.Chance;
    this.Inventaire = item.Inventaire;
    this.Image = item.Image;
    this.Ordre_Colorimetrique = item.Ordre_Colorimetrique;
    this.BonusForce = item.BonusForce;
    this.BonusAgilite = item.BonusAgilite;
    this.BonusIntelligence = item.BonusIntelligence;
    this.BonusVitalite = item.BonusVitalite;
    this.BonusRessource = item.BonusRessource;
    this.BonusArmure = item.BonusArmure;
    this.BonusReussite = item.BonusReussite;
    this.Statistiques = new Statistiques(+this.Id_Personnage,
      +this.team,
      +this.Intelligence, +this.BonusIntelligence,
      +this.Force, +this.BonusForce,
      +this.Agilite, +this.BonusAgilite,
      +this.Vitalite, +this.BonusVitalite,
      +this.Ressource, +this.BonusRessource,
      +this.BonusArmure, +this.BonusArmure);
    this.Statistiques.bonusCombat = new BonusCombat(this.Id_Personnage, this.http);
    this.statistiquesService.setStatistique(this.Statistiques);
  }

  public getCompetencesID() {
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/listeCompetencesAngularRest.php?id='
      + this.Id_Personnage;
    const personnageObservable: Observable<number[]> = this.http.get<number[]>(baseUrlBis).pipe(
      // Adapt each item in the raw data array
      map((data: number[]) => data.map(item => item)),
    );
    const personnageSuscription: Subscription = personnageObservable.subscribe(
      (value) => {
        this.CompetencesID = value;
        personnageSuscription.unsubscribe();
      },
      (error) => { console.log('Une erreur est survenue dans la suscription: ' + error); },
      () => {}
    );
  }

  refreshData() {
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/personnageRest.php?id=' + this.Id_Personnage;
    // console.log('Rcupration personnage');
    const personnageObservable = this.http.get<PersonnageComponent>(baseUrlBis).pipe(
      // Adapt each item in the raw data array
      map((item: PersonnageComponent) => this.adaptFromInside(item)),
    );
    const personnageSuscription: Subscription = personnageObservable.subscribe(
      () => {
        personnageSuscription.unsubscribe();
        this.Statistiques.bonusCombat.loadItSelf();
        this.entities = this.entitiesService.getEntities(this.http, this.combatSession);
      },
      (error) => { console.log('Une erreur est survenue dans la suscription: ' + error); },
      () => {}
    );
  }

  resetCombat() {
    const baseUrlBis = BASE_URL + '/Classes/version_mobile/REST/resetCombat.php?id=' + this.combatSession;
    const combatObservable = this.http.get<PersonnageComponent>(baseUrlBis);
    const combatSuscription: Subscription = combatObservable.subscribe(
      () => {
        combatSuscription.unsubscribe();
        this.refreshData();
      },
      (error) => { console.log('Une erreur est survenue dans la suscription: ' + error); },
      () => {}
    );
  }
}
