import {Component, OnInit} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-combat-session',
  templateUrl: './combatSession.component.html',
  styleUrls: ['./combatSession.component.scss']
})
export class CombatSessionComponent implements OnInit {

  actualCharacterID: number;

  constructor(private http: HttpClient,
              private route: ActivatedRoute) { }

  ngOnInit() {
    this.actualCharacterID = +this.route.snapshot.params.id;
  }

}
